import { FC, useState } from "react";
import { SubmitHandler, useForm } from "react-hook-form";
import { useAppDispatch } from "../../hooks";
import { IItem } from "../../interfaces";
import { itemAction } from "../../redux";
import css from "./itemForm.module.css";

const ItemForm: FC = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false); //      
  const { reset, handleSubmit, register, formState: { errors, isValid } } = useForm<IItem>();
  const dispatch = useAppDispatch();

  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen); //    
  };

  const save: SubmitHandler<IItem> = async (item) => {
    try {
      await dispatch(itemAction.create(item));
      reset();
      setIsMenuOpen(false); //     
    } catch (error) {
      console.error("   :", error);
    }
  };

  return (
    <div className={css.container}>
      <button onClick={toggleMenu}> </button>
      {isMenuOpen && ( //     
        <div className={css.form}>
          <form onSubmit={handleSubmit(save)}>
            {/*<input type="text" placeholder=" ID" {...register("categoryId._id", { required: true })} />*/}
            <input type="text" placeholder={"itemName"} {...register("itemName")} />
            <input type="text" placeholder={""} {...register("brand")} />
            <input type="text" placeholder={""} {...register("article")} />
            <input type="text" placeholder={""} {...register("size")} />
            <input type="text" placeholder={""} {...register("price", { valueAsNumber: true })} />
            <input type="text" placeholder={""} {...register("count")} />
            <button disabled={!isValid}></button>
          </form>
        </div>

      )}
    </div>
  );
};

export { ItemForm };
