import { AxiosError } from "axios";
import { SubmitHandler, useForm } from "react-hook-form";
import { useNavigate } from "react-router-dom";
import React, { FC, useState } from "react";
import { IUser } from "../../interfaces";
import { authService } from "../../services";
import css from "./register.module.css";

const Register: FC = () => {

  const { register, handleSubmit } = useForm<IUser>();
  const [error, setError] = useState(null);
  const registerUser: SubmitHandler<IUser> = async (user) => { //     registerUser
    try {
      await authService.register(user);
    } catch (e) {
    }
  };
  return (
    <div>
      <div>
        <form className={css.form} onSubmit={handleSubmit(registerUser)}>
          <div className={css.card}>
            <div className={css.hBlock}>
              <h1 className={css.h}> </h1>
            </div>
            <div className={css.inputBox}>
              <input className={css.input} type="text" placeholder={"'"} {...register("firstName")} />
            </div>
            <div className={css.inputBox}>
              <input className={css.input} type="text" placeholder={""} {...register("secondName")} />
            </div>
            <div className={css.inputBox}>
              <input className={css.input} type="text" placeholder={"email"} {...register("email")} />
            </div>
            <div className={css.inputBox}>
              <input className={css.input} type="text" placeholder={""} {...register("password")} />
            </div>
            <div className={css.inputBox}>
              <input className={css.input} type="text" placeholder={" "} {...register("phone")} />
            </div>
            <div className={css.inputBox}>
              <button className={css.enter}></button>
            </div>
          </div>
        </form>
      </div>
      {error && <div>{JSON.stringify(error)}</div>}
    </div>
  );
};

export { Register };